.btn {
  font-family: "Gotu", sans-serif;
  font-size: 0.875rem;
  display: inline-block;
  padding: 1rem 5.5rem 1rem 2.5rem;
  background-color: #408F95;
  border: 1px solid #408F95;
  color: #FFFFFF;
  letter-spacing: 0;
  line-height: 1;
  transition: 0.3s;
  cursor: pointer;
  position: relative;
}

.btn span {
  display: block;
}

.btn::before {
  content: "";
  position: absolute;
  display: block;
  background-color: #FFFFFF;
  width: 2.5rem;
  height: 0.0625rem;
  top: 50%;
  right: 2.5rem;
  transform: translateY(-50%);
  transition: 0.3s;
}

.btn::after {
  content: "";
  position: absolute;
  display: block;
  background-color: #FFFFFF;
  width: 0.625rem;
  height: 0.0625rem;
  right: 2.4375rem;
  top: 40%;
  transform: rotate(45deg);
  transition: 0.3s;
}

.btn span::after {
  content: "";
  position: absolute;
  display: block;
  background-color: #FFFFFF;
  border: 1px solid #408F95;
  width: 12.625rem;
  height: 3rem;
  bottom: -0.25rem;
  left: 0.25rem;
  z-index: -1;
}

@media screen and (min-width: 768px) {
  .btn:hover {
    color: #408F95;
    background-color: #FFFFFF;
    opacity: 1;
  }
}

@media screen and (min-width: 768px) {
  .btn:hover::before {
    background-color: #408F95;
    opacity: 1;
  }
}

@media screen and (min-width: 768px) {
  .btn:hover::after {
    background-color: #408F95;
    opacity: 1;
  }
}

